<template>
  <div class="debug">
  <md-app md-mode="flexible">
      <md-app-toolbar class="md-large md-primary">
        <div class="md-toolbar-row">
          <div class="md-toolbar-section-start">
            <md-button class="md-icon-button" @click="menuVisible = !menuVisible">
              <md-icon>menu</md-icon>
            </md-button>
          </div>         

          <div class="md-toolbar-section-end">
            <md-button @click="clear" class="md-icon-button">
              <md-icon>delete</md-icon>
            </md-button>
          </div>
        </div>

        <div class="md-toolbar-row md-toolbar-offset">
          <span class="md-display-1">Debug</span>
        </div>

      </md-app-toolbar>
      <md-app-drawer :md-active.sync="menuVisible">
        <drawer :menuVisible="menuVisible"></drawer>
      </md-app-drawer>
      

      <md-app-content>  
        <md-content>
          <md-list class="md-double-line">
            <RecycleScroller
                class="md-double-line"
                :items="actions"
                :item-height="64"
              >
              <md-list-item @click="() => onChange(item)" slot-scope="{ item }" >
                <md-icon class="md-primary">{{ getItemIcon(item) }}</md-icon>
                <div class="md-list-item-text" v-if="item.EventName">
                  <span>{{ item.EventName }} </span>
                  <span>notification</span>
                </div>
                
                <div class="md-list-item-text" v-if="item.Request">
                  <span>{{ item.Request.Call.method }}({{item.Request.Call.args.join(",") }})</span>
                  <span>Execution time: {{ item.Time / 1000000000 }}</span>
                </div>
                <md-icon class="md-primary">{{ getIcon(item) }}</md-icon> 
              </md-list-item>
            </RecycleScroller>
          </md-list>
        </md-content>

        <md-dialog :md-active.sync="showDialog">
           <md-dialog-title v-if="selectedItem && selectedItem.Request">
              {{ selectedItem.Request.Call.method }}({{selectedItem.Request.Call.args.join(",") }})         
          </md-dialog-title> 
          <md-dialog-title v-else-if="selectedItem && selectedItem.EventName">
            {{ selectedItem.EventName}}
          </md-dialog-title>

          <md-tabs md-dynamic-height class="tab-content">
            <md-tab md-label="General">
              <div v-if="selectedItem">    
                <div v-if="selectedItem.Request">
                    <p>Call ID: {{ selectedItem.Request.ID}}</p>
                    <md-content v-if="selectedItem.Response.Error" class="md-scrollbar, json">
                      <json-tree :data="selectedItem.Response.Error"></json-tree>

                    </md-content>            
                    <md-content v-else class="md-scrollbar, json">
                      <json-tree :data="selectedItem.Response.Success"></json-tree>

                    </md-content>             
                    <md-content> 
                      <p>Excution time: {{ selectedItem.Time / 1000000000 }}</p>
                    </md-content>
                </div>
                <div v-else class="md-scrollbar, json">
                  <json-tree :data="selectedItem.Body"></json-tree>
                </div>
              </div>
            </md-tab>

            <md-tab v-if="selectedItem && selectedItem.Request" md-label="Detail"  class="md-scrollbar, tab-content">
              <md-content class="md-scrollbar, json">
                <json-tree :data="selectedItem"></json-tree>                
              </md-content>
            </md-tab>        
          </md-tabs>

          <md-dialog-actions>
            <md-button class="md-primary" @click="showDialog = false">Close</md-button>
          </md-dialog-actions>
        </md-dialog>
      </md-app-content>

    </md-app>
  </div>
</template>

<script>

/*
        <ui-item v-for="(item, index) in actions"
          :key="index"          
          :item="item"
          :firstIcon="getItemIcon(item)"
          :lastIcon="getIcon(item)">
       
        </ui-item>        
*/
import drawer from './Drawer'

import { mapState, mapActions } from 'vuex'

export default {
  name: 'Debug',
  props: {
  },
  components: {
    drawer,
  },
  methods: {
    clear() {
      this.$store.commit('clearActions')
    },
    getItemIcon(item) {
      if (item.EventName) {
        return 'notifications'
      }
      return 'arrow_forward'
    },
    getIcon(item) {
      if (item.EventName) {
        return ''
      }

      if (item.Response.Success !== undefined && item.Response.Success !== null) {
        return 'done'
      }

      return 'error'
    },
    onChange(item) {
      //const selectedItem = this.actions[item]
      this.showDialog = true
      this.selectedItem = item;
    },
    onConfirm(result) {
      this.open = false;
    }
  },
  computed: mapState(['actions']),
  data: () => ({
    selectedItem: undefined,
    showDialog: false,
    menuVisible: false
  })
}
</script>

