import Vuex from 'vuex'
import Vue from 'vue';
import VuexPersistence from 'vuex-persist'

const vuexLocal = new VuexPersistence({
  storage: window.localStorage
})

Vue.use(Vuex);

function guid() {
  function s4() {
    return Math.floor((1 + Math.random()) * 0x10000)
      .toString(16)
      .substring(1);
  }
  return s4() + s4() + '-' + s4() + '-' + s4() + '-' + s4() + '-' + s4() + s4() + s4();
}

function filterLog(log, levels, text) {
  let valid = true
  if (levels.length > 0) {
    valid = levels.includes(log.Level) & valid
  }

  if (valid && text != "") {
    valid = log.Message.search(text) != -1
  }

  return valid
}

export default new Vuex.Store({
  state: {
    filters: [],
    actions: [],
    logs: [],
    socket: {
      isConnected: false,
      reconnectError: false,
    }
  },
  mutations: {
    deleteLogFilter(state, id) { 
      state.filters = state.filters.filter(it => it.id != id)
    },
    addLogFilter(state, filter) {
      const filterObj = { ...filter, id: guid() }
      state.filters = [...state.filters, filterObj]
    },
    clearLogs(state) {
      state.logs = []
    },
    clearActions(state) {
      state.actions = []
    },
    SOCKET_ONOPEN(state, event) {
      Vue.prototype.$socket = event.currentTarget
      state.socket.isConnected = true
    },
    SOCKET_ONCLOSE(state, event) {
      state.socket.isConnected = false
    },
    SOCKET_ONERROR(state, event) {
    },
    // default handler called for all methods
    SOCKET_ONMESSAGE(state, incomming) {
      const chunks = incomming.data.split("\n")
      const messages = chunks.map(it => JSON.parse(it))

      const calls = messages.filter(it => !it.Log).map(it => ({ id: it.ID, ...it }))
      const logs = messages.filter(it => !!it.Log).map(it => {
        const stack = it.Log.Stack
        let entries = []      
        for (let i = 1; i < stack.length; i+=2) {
          entries.push({
            func: stack[i],
            file: stack[i + 1]
          })
        }

        return { id: it.Log.ID, ...it.Log, entries }
      })


      let newLogs = [...logs, ...state.logs]
      let newCalls = [...calls, ...state.actions]
      if (newLogs.length > 200) {
        newLogs = newLogs.splice(200);
      }

      if (newCalls.length > 50) {
        newLogs = newCalls.splice(50);
      }
      
      state.actions = newCalls
      state.logs = newLogs

    },
    // mutations for reconnect methods
    SOCKET_RECONNECT(state, count) {
    },
    SOCKET_RECONNECT_ERROR(state) {
      state.socket.reconnectError = true;
    },
  },
  getters: {
    getLogsWithFilter: (state) => () => {
      const filters = state.filters

      return state.logs.filter(log => {
      
          return filters.reduce((acc, filter) => {
            return acc && filterLog(log, filter.levels, filter.text)
          }, true)
      })
    }
  },
  actions: {
    sendMessage: function (context, message) {
      Vue.prototype.$socket.send(message)
    }
  },
  plugins: [vuexLocal.plugin]
})