<template>
  <div class="debug">
    <md-app md-waterfall md-mode="fixed-last">
      <md-app-toolbar class="md-large md-dense md-primary">
        <div class="md-toolbar-row">
          <div class="md-toolbar-section-start">
            <md-button class="md-icon-button" @click="menuVisible = !menuVisible">
              <md-icon>menu</md-icon>
            </md-button>

            <span class="md-display-1">Logs</span>
          </div>                  
        </div>

        <div class="md-toolbar-row">
            <div>
                <md-chip class="md-accent"  v-for="filter in filters" :key="filter.id" md-deletable @md-delete="deleteFilter(filter.id)">
                    <span v-if="text != ''">text:{{ filter.text }} </span> 
                    <span v-if="filter.levels.length > 0">levels: {{ filter.levels.join(", ") }}</span>
                  </md-chip>
            </div>         
            <div class="md-toolbar-section-end">
              <md-button @click="addFilter" class="md-icon-button">
                <md-icon>add</md-icon>
              </md-button>
              <md-button @click="clear" class="md-icon-button">
                <md-icon>delete</md-icon>
              </md-button>
            </div>
        </div>
      </md-app-toolbar>
      <md-app-drawer :md-active.sync="menuVisible">
        <drawer :menuVisible="menuVisible"></drawer>
      </md-app-drawer>

      <md-app-content>
        <md-list class="md-double-line">
          <RecycleScroller
              class="md-double-line"
              :items="logs"
              :item-height="64"
            >
            <md-list-item @click="() => onChange(item)" slot-scope="{ item }" >
              <div class="md-list-item-text">
                <span>{{ item.Message }}</span>
                <span>{{ item.Time }}</span>
              </div>          
              <md-chip v-if="item.Level == 'trace'" md-clickable>{{ item.Level }}</md-chip>
              <md-chip v-else-if="item.Level == 'error'" class="md-accent" md-clickable>{{ item.Level }}</md-chip>
              <md-chip v-else class="md-primary" md-clickable>{{ item.Level }}</md-chip>
            </md-list-item>
          </RecycleScroller>

        </md-list>
      </md-app-content>
    </md-app>
    <md-dialog :md-active.sync="showDialog">
      <md-tabs md-dynamic-height class="tab-content">
        <md-tab md-label="General">
          <div class="content">

          <div v-if="selectedItem" class="block">    
            <md-content class="md-elevation-4 text-block">
              <span class="md-title">Message</span>
              <p>{{ selectedItem.Message }}</p>
            </md-content>
            <md-content class="md-elevation-4">
              <div class="md-layout">
                <div class="md-layout-item">
                </div>
              </div>
              <md-list>
                <div v-for="entry in selectedItem.entries">
                  <md-list-item>
                    <div class="md-list-item-text">
                      <p>{{entry.func}}</p>
                      <a @click="onCode(entry.file)">{{entry.file}}</a>
                    </div>
                  </md-list-item>
                  
                  <md-divider></md-divider>    
                </div>   
              </md-list>
            </md-content>
          </div>
          </div>
        </md-tab>
        <md-tab md-label="Details">
          <md-content v-if="selectedItem" class="json">    
            <json-tree :data="selectedItem"></json-tree>
          </md-content>
        </md-tab>
      </md-tabs>

      <md-dialog-actions>
        <md-button class="md-primary" @click="showDialog = false">Close</md-button>
      </md-dialog-actions>
    </md-dialog>

    <md-dialog :md-active.sync="showFilterDialog">
      <md-dialog-title>New filter</md-dialog-title>

      <md-content class="md-content-dialog">
      <div class="md-layout">
        <div class="md-layout-item">
          <md-field>
              <label for="movies">Log levels</label>
              <md-select v-model="selectedLevels" name="movies" id="movies" multiple>
                <md-option value="panic">panic</md-option>
                <md-option value="fatal">fatal</md-option>
                <md-option value="error">error</md-option>
                <md-option value="warn">warn</md-option>
                <md-option value="info">info</md-option>
                <md-option value="debug">debug</md-option>
                <md-option value="trace">trace</md-option>
              </md-select>
          </md-field>

          <md-field>
            <label>Must contain</label>
            <md-input v-model="filterText"></md-input>
            <span class="md-helper-text">Text filter</span>
          </md-field>
        </div>      
      </div>
      </md-content>


      <md-dialog-actions>
        <md-button class="md-primary" @click="showFilterDialog = false">Close</md-button>
        <md-button class="md-primary" @click="commitNewFilter">Add</md-button>
      </md-dialog-actions>
    </md-dialog>

  </div>
</template>

<script>

/*
        <ui-item v-for="(item, index) in actions"
          :key="index"          
          :item="item"
          :firstIcon="getItemIcon(item)"
          :lastIcon="getIcon(item)">
       
        </ui-item>        
*/
import drawer from './Drawer'

import { mapState, mapActions } from 'vuex'

export default {
  name: 'Logs',
  props: {
    ID: String,
  },
  components: {
    drawer,
  },
  methods: {
    deleteFilter(id) {
      this.$store.commit('deleteLogFilter', id)
    },
    commitNewFilter() {
      if (this.filterText != "" || this.selectedLevels.length > 0) {
        const filter = { text: this.filterText, levels: this.selectedLevels }
        this.$store.commit('addLogFilter', filter)
      }

      this.selectedLevels = []
      this.filterText = ""

      this.showFilterDialog = false
    },
    addFilter() {
      this.showFilterDialog = true
    },
    clear() {
      this.$store.commit('clearLogs')
    },
    getLevelColor(level) {
      switch (level) {
        case 'error':
          return 'md-accent'
      }

      return 'md-primary'    
    },

    getItemIcon(item) {
      if (item.EventName) {
        return 'notifications'
      }
      return 'arrow_forward'
    },
    getIcon(item) {
      if (item.EventName) {
        return ''
      }

      if (item.Response.Success !== undefined && item.Response.Success !== null) {
        return 'done'
      }

      return 'error'
    },
    onCode(file) {
      this.$router.push({ path: `/file/`, query: { file }})
    },
    onChange(item) {
      //const selectedItem = this.actions[item]
      const ID = item.ID
      this.$router.push({ path: `/logs/${ID}`})
    },
    onConfirm(result) {
      this.open = false;
    }
  },
  computed: {    
    showDialog: {
      set: function() {
        this.$router.push({ path: `/logs/`})
      },
      get: function() {
        return this.$route.params.ID !== undefined
      },
    },
    selectedItem() {
      // We will see what `params` is shortly
      return this.logs.find(it => it.ID == this.$route.params.ID)
    },
    logs() {
      return this.$store.getters.getLogsWithFilter()
    },
    ...mapState(['filters']),
  },
  data: () => ({
    selectedLevels: [],
    filterText: "",
    filter: [],
    showFilterDialog: false,
    menuVisible: false,
  })
}
</script>


