<template>
  <div class="debug">
  <md-app md-mode="flexible">
      <md-app-toolbar class="md-large md-primary">
        <div class="md-toolbar-row">
          <div class="md-toolbar-section-start">
            <md-button class="md-icon-button" @click="menuVisible = !menuVisible">
              <md-icon>menu</md-icon>
            </md-button>
          </div>         
        </div>

        <div class="md-toolbar-row md-toolbar-offset">
          <span class="md-display-1">File {{ $route.query.file }}</span>
        </div>
      </md-app-toolbar>
      <md-app-drawer :md-active.sync="menuVisible">
        <drawer :menuVisible="menuVisible"></drawer>
      </md-app-drawer>

      <md-app-content>  
        <monaco-editor
          ref="editor"
          style="width: 100%;"
          class="editor"
          @model="change"
          @modelOptions="change"
          :options="options"
          v-model="code">
        </monaco-editor> 
      </md-app-content>
    </md-app>
  </div>
</template>

<script>

/*
        <ui-item v-for="(item, index) in actions"
          :key="index"          
          :item="item"
          :firstIcon="getItemIcon(item)"
          :lastIcon="getIcon(item)">
       
        </ui-item>        
*/
import drawer from './Drawer'

import { mapState, mapActions } from 'vuex'
import MonacoEditor from 'vue-monaco'

export default {
  name: 'file',
  props: {
  },
  components: {
    drawer,
    MonacoEditor,
  },
  data: () => ({
      line: 0,
      code: undefined,
      menuVisible: false,
      reader: new FileReader(),
      options: {
        language: 'go',
        glyphMargin: true,
      },
  }),
  methods: {
    loadBlob() {
      const code = this.reader.result;
      this.code = code
      const editor = this.$refs.editor.getMonaco()
      window.editor = editor
      editor.setValue(code)
      this.change()
    },
    change() {
      const line = this.line
      const editor = this.$refs.editor.getMonaco()

      editor.revealPositionInCenter({ lineNumber: line, column: 1 });

      // editor.revealLineInCenter(this.line);
      editor.deltaDecorations([], [{
          range: new monaco.Range(line,1,line,1),
          options: {
            isWholeLine: true,  
            className: 'myContentClass',
            glyphMarginClassName: 'myGlyphMarginClass'
          }
        }
      ]);      
    },
    init() {
      this.reader.onload = this.loadBlob
      const reader = this.reader
      const file = this.$route.query.file
      const fileName = file.substring(0, file.search(":"))    
      const line = file.substring(file.search(":") + 1, file.search(" "))    
      this.line = Number.parseInt(line)

      fetch('http://localhost:9009/file' + fileName).then(function(response) {
        return response.blob();
      }).then(function(blob) {      
        reader.readAsText(blob);   
      })
    }
  },
  mounted: function () {
    this.init();
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .editor {
    width: 100vh;
    height: 80vh;
  }
  .myGlyphMarginClass {
    background: red;
  }
  .myContentClass {
    background: lightblue;
  }

</style>
